.paginationall {
  height: 100%;
  width: 100%;
  padding: 10vh;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  /*modal-2*/
  /*modal-3*/
  /*modal-4*/
  /* modal-5 */
  /*modal-6*/
}
.paginationall ul li {
  list-style: none;
  float: left;
}
.paginationall a:hover,
.paginationall a {
  text-decoration: none;
}
.paginationall a {
  display: inline-block;
  height: 40px;
  width: 40px;
  text-align: center;
  line-height: 40px;
  vertical-align: middle;
}
.paginationall .active {
  cursor: default;
}
.paginationall .modal-1 li + li,
.paginationall .modal-2 li + li {
  border-left: 1px solid #e4e3e3;
}
.paginationall .modal-1 li a,
.paginationall .modal-2 li a {
  color: #4285F4;
  background-color: #fff;
}
.paginationall .modal-1 li a:hover {
  background-color: #eeeeee;
}
.paginationall .modal-1 li a:active {
  background-color: #4285F4;
  color: #fff;
}
.paginationall .modal-1 .active:hover,
.paginationall .modal-1 .active {
  background-color: #4285F4;
  color: #fff;
}
.paginationall .modal-1 li:first-child a {
  border-radius: 10px 0 0 10px;
}
.paginationall .modal-1 li:last-child a {
  border-radius: 0 10px 10px 0;
}
.paginationall .modal-2 li a {
  color: #aaa6a6;
}
.paginationall .modal-2 li:first-child a {
  border-radius: 20px 0 0 20px;
}
.paginationall .modal-2 li:last-child a {
  border-radius: 0 20px 20px 0;
}
.paginationall .modal-2 .active:hover,
.paginationall .modal-2 .active {
  background-color: #E34E48;
  color: #fff;
}
.paginationall .modal-2 li a:active {
  background-color: #E34E48;
  color: #fff;
}
.paginationall .modal-2 li a:hover {
  background-color: #eeeeee;
  color: #E34E48;
}
.paginationall .modal-3 li a,
.paginationall .modal-4 li a {
  color: #fff;
  border-radius: 50%;
  margin: 0 5px;
}
.paginationall .modal-3 .active:hover,
.paginationall .modal-3 .active {
  background-color: #37B247;
  color: #fff;
}
.paginationall .modal-3 li a:active {
  background-color: #37B247;
  color: #fff;
}
.paginationall .modal-3 li a:hover {
  background-color: #37B247;
}
.paginationall .modal-4 li a {
  background-color: #F7C12C;
}
.paginationall .modal-4 .active:hover,
.paginationall .modal-4 .active {
  background-color: #FFA100;
  color: #fff;
}
.paginationall .modal-4 li a:active {
  background-color: #FFA100;
  color: #fff;
}
.paginationall .modal-4 li a:hover {
  background-color: #FFA100;
}
.paginationall .modal-4 li:first-child a {
  border-radius: 20px 0 0 20px;
  width: 150px;
}
.paginationall .modal-4 li:last-child a {
  border-radius: 0 20px 20px 0;
  width: 150px;
}
.paginationall .modal-5 {
  position: relative;
}
.paginationall .modal-5 li a {
  position: relative;
  padding: 10px 0 0;
  margin: 0 5px;
  color: #a2a4a7;
}
.paginationall .modal-5:before {
  content: '';
  position: absolute;
  width: 100%;
  height: 45px;
  top: 5px;
  z-index: -1;
  background-image: linear-gradient(to right, #000000 0%, #000000 40%, #000000 50%, #000000 60%, #000000 100%);
}
.paginationall .modal-5 li:first-child a,
.paginationall .modal-5 li:last-child a {
  color: #4285F4;
}
.paginationall .modal-5 a:hover:after {
  content: '';
  position: absolute;
  width: 40px;
  height: 40px;
  left: 0px;
  top: 8px;
  z-index: -2;
  background-color: #4688f3;
  border-radius: 50%;
  color: #fff;
}
.paginationall .modal-5 li:first-child a:hover,
.paginationall .modal-5 li:last-child a:hover {
  color: #ffffff;
}
.paginationall .modal-5 li:first-child a:hover:after,
.paginationall .modal-5 li:last-child a:hover:after {
  display: none;
}
.paginationall .modal-5 .active {
  background-color: #4285F4;
}
.paginationall .modal-5 .active:before {
  position: absolute;
  content: '';
  border: 5px solid transparent;
  width: 40px;
  top: -5px;
  border-bottom: 5px solid #4285F4;
  left: -5px;
}
.paginationall .modal-5 .active:hover:after {
  display: none;
}
.paginationall .modal-6 a {
  background-color: #fff;
}
.paginationall .modal-6 li:first-child a {
  border-radius: 20px 0 0 20px;
  padding: 0 10px;
  background-image: linear-gradient(to left, #000000 0%, #000000 10%);
}
.paginationall .modal-6 li:last-child a {
  border-radius: 0 20px 20px 0;
  padding: 0 10px;
  background-image: linear-gradient(to right, #000000 0%, #000000 10%);
}
.paginationall .modal-6 .active {
  background-color: #fff;
  color: #E34E48;
  box-shadow: 1px 1px 1px #535050 inset;
}
.paginationall .modal-6 li a:active {
  color: #E34E48;
}
.paginationall .modal-6 li a:hover {
  background-color: #eeeeee;
  color: #E34E48;
}
.paginationall .modal-6 li + li {
  border-left: 1px solid #e4e3e3;
}
