#skill {
  list-style: none;
  width: 70%;
  height: 70%;
  margin: auto;
  margin-top: 10%;
}
#skill li span {
  display: block;
  height: 30px;
  margin-top: -10px;
  border-radius: 15px;
}
#skill li span:after {
  content: '';
  display: block;
  position: relative;
  top: -1px;
  left: -1px;
  width: 50vw;
  height: 32px;
  background-color: #dce3f8;
  box-shadow: 0 1px #868686 inset;
  margin-top: -10px;
  border-radius: 15px;
  z-index: -1;
}
#skill .html-css {
  -webkit-animation: html-css 1.5s ease-in-out;
          animation: html-css 1.5s ease-in-out;
  background-color: #0000A0;
  width: 100%;
}
#skill .javascript {
  -webkit-animation: javascript 1.5s ease-in-out;
          animation: javascript 1.5s ease-in-out;
  background-color: #1F6ED4;
  width: 80%;
}
#skill .react {
  -webkit-animation: react 1.5s ease-in-out;
          animation: react 1.5s ease-in-out;
  background-color: #3BB9E9;
  width: 70%;
}
#skill .vue {
  -webkit-animation: vue 1.5s ease-in-out;
          animation: vue 1.5s ease-in-out;
  background: #B9EDF8;
  width: 60%;
}
@-webkit-keyframes html-css {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}
@keyframes html-css {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}
@-webkit-keyframes javascript {
  from {
    width: 0;
  }
  to {
    width: 80%;
  }
}
@keyframes javascript {
  from {
    width: 0;
  }
  to {
    width: 80%;
  }
}
@-webkit-keyframes react {
  from {
    width: 0;
  }
  to {
    width: 70%;
  }
}
@keyframes react {
  from {
    width: 0;
  }
  to {
    width: 70%;
  }
}
@-webkit-keyframes vue {
  from {
    width: 0;
  }
  to {
    width: 60%;
  }
}
@keyframes vue {
  from {
    width: 0;
  }
  to {
    width: 60%;
  }
}
